.addtodo-container {
  padding: 0 1rem;
  width: 100%;
}

.addtodo {
  width: 100%;
  background-color: #fff;
  border-radius: 9px;
  box-shadow: 0 0 5px 1px rgba(0, 0, 0, 0.2);
  display: flex;
}

.addtodo--form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex-grow: 1;
  padding: 1rem 2rem;
}

.addtodo--text {
  outline: none;
  border: none;
  box-shadow: 0 0 0px 2px rgb(202, 202, 202);
  padding: 1.5rem 2rem;
  border-radius: 12px;
  font-size: 2.2rem;
}

.addtodo--text:focus {
  box-shadow: 0 0 0px 5px rgb(202, 202, 202);
}

.addtodo--label {
  margin-top: 2rem;
  margin-bottom: 1rem;
  font-size: 2rem;
  font-weight: 300;
}

.addtodo--duedate {
  outline: none;
  border: none;
  box-shadow: 0 0 0px 2px rgb(202, 202, 202);
  padding: 1.2rem;
  border-radius: 12px;
  font-size: 1.8rem;
  width: 40%;
}

.addtodo--button {
  border: none;
  width: 5rem;
  flex-shrink: 0;
  cursor: pointer;

  box-shadow: 0px 2px 5px 1px rgba(0, 0, 0, 0.5);
  transition: all 0.15s;

  margin-left: auto;
  background-color: #6ede8a;
  border-top-right-radius: 9px;
  border-bottom-right-radius: 9px;
}

.addtodo--button:hover {
  transform: translateY(-6px);
  box-shadow: 0 8px 5px 1px rgba(0, 0, 0, 0.5);
}

.addtodo--button:active {
  box-shadow: 0 2px 5px 1px rgba(0, 0, 0, 0.5);
  transform: translateY(-2px);
}

.addtodo--icon {
  width: 30px;
  height: 30px;
  stroke-width: 50;
  stroke: #333;
}

@media only screen and (max-width: 48em) {
  .addtodo--icon {
    width: 20px;
    height: 20px;
  }

  .addtodo--duedate {
    font-size: 1.6rem;
  }

  .addtodo--label {
    font-size: 1.6rem;
  }

  .addtodo--text {
    font-size: 2rem;
  }
}

@media only screen and (max-width: 32em) {
  .addtodo--duedate {
    width: 100%;
  }
}

@media only screen and (max-width: 25em) {
  .addtodo--duedate {
    font-size: 1.8rem;
  }

  .addtodo--label {
    font-size: 1.8rem;
  }
}
